.c3-header {
  position: sticky;
  top: 0;
  z-index: var(--z-sticky);
  height: var(--header-height);
  background: var(--header-bg);
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--header-border);
  transition: background-color var(--transition-normal), border-color var(--transition-normal);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  text-decoration: none;
  color: var(--text-primary);
  transition: opacity var(--transition-fast);
}
.brand:hover {
  opacity: 0.8;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: linear-gradient(135deg, var(--c3-primary) 0%, var(--c3-primary-dark) 100%);
  border-radius: var(--border-radius-md);
  font-size: 1.25rem;
}

.logo-icon {
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
}

.brand-text {
  font-family: var(--font-heading);
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
@media (max-width: 768px) {
  .nav-links {
    display: none;
  }
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.5rem 1rem;
  font-family: var(--font-body);
  font-size: var(--text-sm);
  font-weight: 500;
  color: var(--text-secondary);
  text-decoration: none;
  border-radius: var(--border-radius-md);
  transition: all var(--transition-fast);
}
.nav-link:hover {
  color: var(--text-primary);
  background: var(--sidebar-link-hover);
}
.nav-link.active {
  color: var(--c3-primary);
  background: var(--sidebar-link-active);
}

.nav-link-external .external-icon {
  font-size: 14px;
  width: 14px;
  height: 14px;
  opacity: 0.7;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.action-btn {
  color: var(--text-secondary);
  transition: color var(--transition-fast), background-color var(--transition-fast);
}
.action-btn:hover {
  color: var(--text-primary);
  background: var(--sidebar-link-hover);
}
.action-btn .mat-icon {
  font-size: 20px;
  width: 20px;
  height: 20px;
}

.search-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all var(--transition-normal);
}

.search-input {
  width: 0;
  padding: 0;
  border: none;
  background: transparent;
  font-family: var(--font-body);
  font-size: var(--text-sm);
  color: var(--text-primary);
  outline: none;
  opacity: 0;
  transition: all var(--transition-normal);
}
.search-input::placeholder {
  color: var(--text-muted);
}

.search-open {
  background: var(--surface-elevated);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  padding-left: 1rem;
}
.search-open .search-input {
  width: 200px;
  opacity: 1;
}

.theme-toggle .mat-icon {
  transition: transform var(--transition-fast);
}
.theme-toggle:hover .mat-icon {
  transform: rotate(15deg);
}
.theme-toggle:active .mat-icon {
  transform: scale(0.9);
}

@media (max-width: 640px) {
  .brand-text {
    display: none;
  }
  .header-content {
    padding: 0 1rem;
  }
}
